import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormBuilder, FormGroup} from "@angular/forms";
import {debounceTime} from "rxjs";
import { KonversoService } from '../konverso.service';
import { TranslateService } from '../translate.service';
//import {AnimationsService, FadeSlideInOut} from "@core/animations";

type ICalendarTemplate = { [key: string]: { [key: string]: boolean } };

@Component({
  selector: 'custom-checkbox-calendar',
  templateUrl: './custom-checkbox-calendar.component.html',
  styleUrls: ['./custom-checkbox-calendar.component.scss'],
  //animations : [FadeSlideInOut]
})
export class CustomCheckboxCalendarComponent implements OnInit {

  @Input() calendarTemplate: ICalendarTemplate = {
    morning: {monday: false, tuhesday: false}
  }
  @Input() columnOverride: { [key: string]: string } = {};
  @Output() calendarChange = new EventEmitter(true);
  @Input() state :boolean = true;

  public form: { [key: string]: FormGroup } = {};
  public column: string[] = [];
  public rows: string[] = [''];
  private checked: { [key: string]: boolean } = {};
  private calendar_value: any = {}

  constructor(
    private _formBuilder: FormBuilder, 
    //public animations : AnimationsService,
    public translate: TranslateService, 
    public service: KonversoService,
  ) {
  }

  ngOnInit(): void {
    const formGroup: ICalendarTemplate = {};
    for (const part in this.calendarTemplate) {
      if (!formGroup[part]) {
        formGroup[part] = {};// morning
      }
      this.rows.push(part);
      for (const day in this.calendarTemplate[part]) {
        // @ts-ignore
        formGroup[part][day] = [this.calendarTemplate[part][day]] //morgin/monday
        this.column.push(day);
      }
      this.column = [...new Set(this.column)];
    }

    for (const part in formGroup) {
      this.form[part] = this._formBuilder.group(formGroup[part]);
      this.observer(part);
      this.process(this.form[part].value, part);
    }

  }

  private observer(part: string) {
    this.form[part].valueChanges.pipe(debounceTime(200)).subscribe(changes => this.process(changes, part))
  }

  private process(changes: any, part: string) {
    if (changes.all !== this.checked[part]) {
      const keys = Object.keys(this.form[part].controls);
      for (const k of keys) {
        this.form[part].controls[k].patchValue(changes.all, {emitEvent: false, onlySelf: true});
        if (!this.calendar_value[k]) this.calendar_value[k] = {}
        if (!this.calendar_value[k][part]) this.calendar_value[k][part] = changes.all;
        else this.calendar_value[k][part] = changes.all;
        this.calendar_value[k][part] = changes.all;
      }
      this.checked[part] = changes.all;
    } else {
      const keys = Object.keys(changes).filter((e: string) => e !== 'all');
      let counter = keys.length;
      for (const k of keys) {
        if (!this.calendar_value[k]) this.calendar_value[k] = {}
        if (!this.calendar_value[k][part]) this.calendar_value[k][part] = changes[k];
        else this.calendar_value[k][part] = changes[k]
        if (!changes[k]) {
          counter--;
        }
      }
      if (counter === keys.length) {
        this.form[part].controls['all'].patchValue(true, {emitEvent: false, onlySelf: true});
        this.checked[part] = true;
      } else {
        this.checked[part] = false;
        this.form[part].controls['all'].patchValue(false, {emitEvent: false, onlySelf: true});
      }
    }
    this.calendarChange.emit(this.calendar_value)
  }

}
