const objectToString = Object.prototype.toString;
const isArrayPolyfill = (object: any) => {
    return objectToString.call(object) === '[object Array]';
};

const isFunction = (object: any) => {
    return typeof object === 'function';
};

const typeStr = (obj: any) => {
    return isArray(obj) ? 'array' : typeof obj;
};

const escapeRegExp = (str: any) => {
    return str.replace(/[\-\[\]{}()*+?.,\\\^$|#\s]/g, '\\$&');
};

const hasProperty = (obj, propName) => {
    return obj != null && typeof obj === 'object' && (propName in obj);
};

const primitiveHasOwnProperty = (primitive: any, propName: any) => {
    return (
        primitive != null
        && typeof primitive !== 'object'
        && primitive.hasOwnProperty
        && primitive.hasOwnProperty(propName)
    );
};

const isArray = Array.isArray || isArrayPolyfill;

const regExpTest = RegExp.prototype.test;

const testRegExp = (regEx: RegExp, str: string) => {
    return regExpTest.call(regEx, str);
};

const nonSpaceRe = /\S/;

const isWhitespace = (str: string) => {
    return !testRegExp(nonSpaceRe, str);
};

const entityMap = {
    '&': '&amp;',
    '<': '&lt;',
    '>': '&gt;',
    '"': '&quot;',
    '\'': '&#39;',
    '/': '&#x2F;',
    '`': '&#x60;',
    '=': '&#x3D;'
};

const escapeHtml = (str: string) => {
    return String(str).replace(/[&<>"'`=\/]/g, (s: string) => {
        return entityMap[s];
    });
};

const whiteRe = /\s*/;
const spaceRe = /\s+/;
const equalsRe = /\s*=/;
const curlyRe = /\s*\}/;
const tagRe = /#|\^|\/|>|\{|&|=|!/;

const squashTokens = (tokens: any) => {
    const squashedTokens = [];

    let token, lastToken;
    for (let i = 0, numTokens = tokens.length; i < numTokens; ++i) {
        token = tokens[i];

        if (token) {
            if (token[0] === 'text' && lastToken && lastToken[0] === 'text') {
                lastToken[1] += token[1];
                lastToken[3] = token[3];
            } else {
                squashedTokens.push(token);
                lastToken = token;
            }
        }
    }

    return squashedTokens;
};

const nestTokens = (tokens: any) => {
    const nestedTokens = [];
    let collector = nestedTokens;
    const sections = [];

    let token, section;
    for (let i = 0, numTokens = tokens.length; i < numTokens; ++i) {
        token = tokens[i];

        switch (token[0]) {
            case '#':
            case '^':
                collector.push(token);
                sections.push(token);
                collector = token[4] = [];
                break;
            case '/':
                section = sections.pop();
                section[5] = token[2];
                collector = sections.length > 0 ? sections[sections.length - 1][4] : nestedTokens;
                break;
            default:
                collector.push(token);
        }
    }

    return nestedTokens;
};

const parseTemplate = (template: any, tags: any) => {
    if (!template) {
        return [];
    }
    let lineHasNonSpace = false;
    let sections = [];     // Stack to hold section tokens
    let tokens = [];       // Buffer to hold the tokens
    let spaces = [];       // Indices of whitespace tokens on the current line
    let hasTag = false;    // Is there a {{tag}} on the current line?
    let nonSpace = false;  // Is there a non-space char on the current line?
    let indentation = '';  // Tracks indentation for tags that use it
    let tagIndex = 0;      // Stores a count of number of tags encountered on a line

    // Strips all whitespace tokens array for the current line
    // if there was a {{#tag}} on it and otherwise only space.
    const stripSpace = () => {
        if (hasTag && !nonSpace) {
            while (spaces.length) {
                delete tokens[spaces.pop()];
            }
        } else {
            spaces = [];
        }

        hasTag = false;
        nonSpace = false;
    };


    const compileTags = (tagsToCompile) => {
        if (typeof tagsToCompile === 'string') {
            tagsToCompile = tagsToCompile.split(spaceRe, 2);
        }

        if (!isArray(tagsToCompile) || tagsToCompile.length !== 2) {
            throw new Error('Invalid tags: ' + tagsToCompile);
        }

        const openingTagRe = new RegExp(escapeRegExp(tagsToCompile[0]) + '\\s*');
        const closingTagRe = new RegExp('\\s*' + escapeRegExp(tagsToCompile[1]));
        const closingCurlyRe = new RegExp('\\s*' + escapeRegExp('}' + tagsToCompile[1]));
        return {openingTagRe, closingTagRe, closingCurlyRe};
    };



    let {openingTagRe, closingTagRe, closingCurlyRe} = compileTags(tags || BARBE.tags);
    console.log('--------------_> TAG DEBUG', openingTagRe, closingTagRe, closingCurlyRe)
    const scanner = new Scanner(template);

    let start, type, value, chr, token, openSection;
    while (!scanner.eos()) {
        start = scanner.pos;

        // Match any text between tags.
        value = scanner.scanUntil(openingTagRe);

        if (value) {
            for (let i = 0, valueLength = value.length; i < valueLength; ++i) {
                chr = value.charAt(i);

                if (isWhitespace(chr)) {
                    spaces.push(tokens.length);
                    indentation += chr;
                } else {
                    nonSpace = true;
                    lineHasNonSpace = true;
                    indentation += ' ';
                }

                tokens.push(['text', chr, start, start + 1]);
                start += 1;

                // Check for whitespace on the current line.
                if (chr === '\n') {
                    stripSpace();
                    indentation = '';
                    tagIndex = 0;
                    lineHasNonSpace = false;
                }
            }
        }

        // Match the opening tag.
        if (!scanner.scan(openingTagRe)) {
            break;
        }

        hasTag = true;

        // Get the tag type.
        type = scanner.scan(tagRe) || 'name';
        scanner.scan(whiteRe);

        // Get the tag value.
        if (type === '=') {
            value = scanner.scanUntil(equalsRe);
            scanner.scan(equalsRe);
            scanner.scanUntil(closingTagRe);
        } else if (type === '{') {
            value = scanner.scanUntil(closingCurlyRe);
            scanner.scan(curlyRe);
            scanner.scanUntil(closingTagRe);
            type = '&';
        } else {
            value = scanner.scanUntil(closingTagRe);
        }

        // Match the closing tag.
        if (!scanner.scan(closingTagRe)) {
            throw new Error('Unclosed tag at ' + scanner.pos);
        }

        if (type === '>') {
            token = [type, value, start, scanner.pos, indentation, tagIndex, lineHasNonSpace];
        } else {
            token = [type, value, start, scanner.pos];
        }
        tagIndex++;
        tokens.push(token);

        if (type === '#' || type === '^') {
            sections.push(token);
        } else if (type === '/') {
            // Check section nesting.
            openSection = sections.pop();

            if (!openSection) {
                throw new Error('Unopened section "' + value + '" at ' + start);
            }

            if (openSection[1] !== value) {
                throw new Error('Unclosed section "' + openSection[1] + '" at ' + start);
            }
        } else if (type === 'name' || type === '{' || type === '&') {
            nonSpace = true;
        } else if (type === '=') {
            // Set the tags for the next time around.
            compileTags(value);
        }
    }

    stripSpace();

    // Make sure there are no open sections when we're done.
    openSection = sections.pop();

    if (openSection) {
        throw new Error('Unclosed section "' + openSection[1] + '" at ' + scanner.pos);
    }

    return nestTokens(squashTokens(tokens));
};


class Scanner {
    public pos: number = 0;
    private string: string;
    private tail: string;

    constructor(str: string) {
        this.string = str;
        this.tail = str;
    }

    public eos() {
        return this.tail === '';
    }

    public scan(regExp: RegExp) {
        const match = this.tail.match(regExp);

        if (!match || match.index !== 0) {
            return '';
        }

        const str: string = match[0];

        this.tail = this.tail.substring(str.length);
        this.pos += str.length;

        return str;
    }

    public scanUntil(regExp: RegExp) {
        const index = this.tail.search(regExp);
        let match;

        switch (index) {
            case -1:
                match = this.tail;
                this.tail = '';
                break;
            case 0:
                match = '';
                break;
            default:
                match = this.tail.substring(0, index);
                this.tail = this.tail.substring(index);
        }

        this.pos += match.length;

        return match;
    }
}

class Context {
    private readonly view: any;
    private cache: any;
    private parent: any;


    constructor(view: any, parentContext: any) {
        this.view = view;
        this.cache = {'.': this.view};
        this.parent = parentContext;
    }

    push(view: any) {
        return new Context(view, this);
    }

    lookup(name: string) {
        const cache = this.cache;

        let value;
        if (cache.hasOwnProperty(name)) {
            value = cache[name];
        } else {
            let context: any = this;
            let intermediateValue: any = false;
            let names: any = false;
            let index: any = false;
            let lookupHit: any = false;

            while (context) {
                if (name.indexOf('.') > 0) {
                    intermediateValue = context.view;
                    names = name.split('.');
                    index = 0;
                    while (intermediateValue != null && index < names.length) {
                        if (index === (names.length - 1)) {
                            lookupHit = (
                                hasProperty(intermediateValue, names[index])
                                || primitiveHasOwnProperty(intermediateValue, names[index])
                            );
                        }

                        intermediateValue = intermediateValue[names[index++]];
                    }
                } else {
                    intermediateValue = context.view[name];
                    lookupHit = hasProperty(context.view, name);
                }
                if (lookupHit) {
                    value = intermediateValue;
                    break;
                }
                context = context.parent;
            }
            cache[name] = value;
        }
        if (isFunction(value)) {
            value = value.call(this.view);
        }
        return value;
    }
}

class Cache {
    private _cache = {};

    public set(key: string, value: any) {
        this._cache[key] = value;
    }

    public get(key: string) {
        return this._cache[key];
    }

    public clear() {
        this._cache = {};
    }
}

class Writer {
    public templateCache = new Cache();

    public clearCache() {
        if (typeof this.templateCache !== 'undefined') {
            this.templateCache.clear();
        }
    }

    public parse(template: any, tags: any) {
        const cache = this.templateCache;
        const cacheKey = template + ':' + (tags || BARBE.tags).join(':');
        const isCacheEnabled = typeof cache !== 'undefined';
        let tokens = isCacheEnabled ? cache.get(cacheKey) : undefined;

        if (tokens === undefined) {
            tokens = parseTemplate(template, tags);
            isCacheEnabled && cache.set(cacheKey, tokens);
        }
        return tokens;
    }

    public render(template: any, view: any, partials: any, config: any) {
        const tags = this.getConfigTags(config);
        const tokens = this.parse(template, tags);
        const context = (view instanceof Context) ? view : new Context(view, undefined);
        return this.renderTokens(tokens, context, partials, template, config);
    }


    private getConfigTags(config: any) {
        if (isArray(config)) {
            return config;
        } else if (config && typeof config === 'object') {
            return config.tags;
        } else {
            return undefined;
        }
    }


    private rawValue(token: any[]) {
        return token[1];
    };

    private renderTokens(tokens: any, context: any, partials: any, originalTemplate: any, config: any) {
        let buffer = '';
        let token;
        let symbol;
        let value;
        for (let i = 0, numTokens = tokens.length; i < numTokens; ++i) {
            value = undefined;
            token = tokens[i];
            symbol = token[0];

            if (symbol === '#') {
                value = this.renderSection(token, context, partials, originalTemplate, config);
            } else if (symbol === '^') {
                value = this.renderInverted(token, context, partials, originalTemplate, config);
            } else if (symbol === '>') {
                value = this.renderPartial(token, context, partials, config);
            } else if (symbol === '&') {
                value = this.unescapedValue(token, context);
            } else if (symbol === 'name') {
                value = this.escapedValue(token, context, config);
            } else if (symbol === 'text') {
                value = this.rawValue(token);
            }

            if (value !== undefined) {
                buffer += value;
            }
        }
        return buffer;
    }

    private renderSection(token: any[], context: any, partials: any, originalTemplate: any, config: any) {
        const self = this;
        let buffer = '';
        let value = context.lookup(token[1]);

        const subRender = (template) => {
            return self.render(template, context, partials, config);
        };

        if (!value) {
            return;
        }

        if (isArray(value)) {
            for (var j = 0, valueLength = value.length; j < valueLength; ++j) {
                buffer += this.renderTokens(token[4], context.push(value[j]), partials, originalTemplate, config);
            }
        } else if (typeof value === 'object' || typeof value === 'string' || typeof value === 'number') {
            buffer += this.renderTokens(token[4], context.push(value), partials, originalTemplate, config);
        } else if (isFunction(value)) {
            if (typeof originalTemplate !== 'string') {
                throw new Error('Cannot use higher-order sections without the original template');
            }

            // Extract the portion of the original template that the section contains.
            value = value.call(context.view, originalTemplate.slice(token[3], token[5]), subRender);

            if (value != null) {
                buffer += value;
            }
        } else {
            buffer += this.renderTokens(token[4], context, partials, originalTemplate, config);
        }
        return buffer;
    }


    private renderInverted(token: any[], context: any, partials: any, originalTemplate: any, config: any) {
        const value = context.lookup(token[1]);

        if (!value || (isArray(value) && value.length === 0)) {
            return this.renderTokens(token[4], context, partials, originalTemplate, config);
        }
    };


    private indentPartial(partial: any, indentation: any, lineHasNonSpace: any) {
        const filteredIndentation = indentation.replace(/[^ \t]/g, '');
        const partialByNl = partial.split('\n');
        for (let i = 0; i < partialByNl.length; i++) {
            if (partialByNl[i].length && (i > 0 || !lineHasNonSpace)) {
                partialByNl[i] = filteredIndentation + partialByNl[i];
            }
        }
        return partialByNl.join('\n');
    };


    private renderPartial(token: any[], context: any, partials: any, config: any) {
        if (!partials) {
            return;
        }
        const tags = this.getConfigTags(config);

        const value = isFunction(partials) ? partials(token[1]) : partials[token[1]];
        if (value !== null) {
            const lineHasNonSpace = token[6];
            const tagIndex = token[5];
            const indentation = token[4];
            let indentedValue = value;
            if (tagIndex === 0 && indentation) {
                indentedValue = this.indentPartial(value, indentation, lineHasNonSpace);
            }
            const tokens = this.parse(indentedValue, tags);
            return this.renderTokens(tokens, context, partials, indentedValue, config);
        }
    }

    private unescapedValue(token: any[], context: any) {
        const value = context.lookup(token[1]);
        if (value !== null) {
            return value;
        }
    }

    private getConfigEscape(config: any) {
        return config && typeof config === 'object' && !isArray(config) ? config.escape : undefined;
    }

    private escapedValue(token: any[], context: any, config: any) {
        const escape = this.getConfigEscape(config) || BARBE.escape;
        const value = context.lookup(token[1]);
        if (value != null) {
            return (typeof value === 'number' && escape === BARBE.escape) ? String(value) : escape(value);
        }
    }
}


export class BARBE {
    public static tags = ['{{', '}}'];
    public static escape: any = escapeHtml;
    public static Scanner: any = Scanner;
    public static Context: any = Context;
    public static Writer: any = Writer;
    public name = 'MY F**KING MUSTACHE LIBRARY FOR ANGULAR';
    public version = 'ECLATER';
    private _Writer: any = new Writer();

    get templateCache() {
        return this._Writer.templateCache;
    }

    set templateCache(cache: any) {
        this._Writer.templateCache = cache;
    }

    public parse(template: any, tags: any) {
        return this._Writer.parse(template, tags);
    }

    public render(template: any, view: any, partials?: any, config?: any) {
        if (typeof template !== 'string') {
            throw new TypeError('Invalid template! Template should be a "string" ' +
                'but "' + typeStr(template) + '" was given as the first ' +
                'argument for mustache#render(template, view, partials)');
        }

        return this._Writer.render(template, view, partials, config);
    };


    public clearCache() {
        this._Writer.clearCache();
    }
}
